* {
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #fafafa;
	color: #212121;
	line-height: 1.5;
}

#search-box {
	font: inherit;
	padding: 8px;
	max-width: 320px;
	margin-bottom: 16px;
}

.country-list {
	list-style: none;
}

.country-list>li {
	display: flex;
	align-items: center;
}

.country {
	display: inline;
	font-weight: bold;
	font-size: 24px;
	margin-left: 25px;
}

.country__wrapper {
	display: flex;
	align-items: center;
}